@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  padding: 0;
  margin: 0
}
.koulen {
  font-family: 'koulen';
}
.arial {
  font-family: Arial, Helvetica, sans-serif;
}
.truncate-2{
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.truncate-3{
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.truncate-4{
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.truncate-5{
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.tag {
  padding-left: 6px;
  padding-right: 6px;
}
.tag:hover > .tag-el{
  text-decoration: underline;
  color: #f8fafc;
}
.tooltip .tooltip-text{
  visibility: hidden;
  width: fit-content;
  background-color: #94A3B8;
  padding: 3px 20px;
  position: absolute;
  color: #002e3f;
  text-align: center;
  border-radius: 6px;
  left: 50%;
  margin-top: 5px;
  transform: translateX(-50%);
  font-size: 14px;
}
.tooltip:hover .tooltip-text {
  visibility: visible;
}
.tooltip .tooltip-text::after{
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #94A3B8 transparent;
}
.login-btn .slide-down {
  height: 0%;
}
.login-btn:hover .slide-down {
  height: 100%;
}
.mouse-dust {
  width: 110vw;
  height: 110vh;
}

@font-face {
  font-family: 'koulen';
  src: url(./assets/Koulen-Regular.woff)   
}